@import "../partials/variables";

//Style 08
#atw_style8{
    .atw_tabs{
        .nav{
            display: flex;
            flex-wrap: wrap;
            padding-bottom:30px;
            margin-bottom: 10px;
            border-bottom: 1px solid $borderColor;
            li{
                margin-right: 30px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    font-size: 22px;
                    color: #9192a3;
                    transition: $transition;
                    display: block;
                    &.active{
                        color: $theme2;
                    }
                }
            }
        }
        .tab-content{
            .tab-pane{
                .atw_slider_wrapper{
                    .owl-stage-outer{
                        padding: 15px 0;
                    }
                    .atw--single_item{
                        transition: $transition;
                        .atw_item_top{
                            img{
                                width: 100%;
                            }
                            .badge--left{
                                top: 20px;
                                left: 20px;
                            }
                            .badge--right{
                                top: 20px;
                                right: 20px;
                            }
                        }
                        .atw_item_bottom{
                            .atw_item_header{
                                margin-bottom: 15px;
                            }
                            .atw_item_info{
                                padding-bottom: 15px;
                                margin: 0 15px;
                            }
                            .atw_product_actions2{
                                transition: 0.3s cubic-bezier(.17,.67,.82,.66);
                                height: 0;
                                overflow: hidden;
                                opacity: 0;
                                padding: 0;
                            }
                        }
                        &:hover{
                            box-shadow:0 10px 10px rgba(84,77,124,0.1);
                            .atw_item_bottom{
                                .atw_item_info{
                                    border-bottom: 1px solid $borderColor;
                                }
                                .atw_product_actions2{
                                    height: auto;
                                    padding: 15px 0;
                                    overflow: visible;
                                    opacity: 1;
                                }
                            }
                        }
                    }
                    .atw_slider_controls{
                        .slider--control8a,
                        .slider--control8b,
                        .slider--control8c{
                            position: absolute;
                            right: 0;
                            margin: 0 4px 0 4px;
                            top: 0;
                            transform: none;
                            border-radius: 0;
                            transition: 0.3s ease;
                            border: 1px solid $borderColor;
                            &:hover{
                                border-color: $theme2;
                                color: $theme2;
                            }
                            &.icon-arrow-left{
                                left: auto;
                                right: 60px;
                            }
                            &.icon-arrow-right{
                                right: 11px;
                            }
                        }
                    }
                }
            }
        }
    }
}